.container_header {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 10px;
}

.header {
  height: 216px;
  background-color: #0000008f;
  background-image: url("header-mobile-min.a0a191ab.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  top: 0;
  left: 0;
}

.header_wraper {
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  padding-bottom: 54px;
  display: flex;
}

.logo-film > svg, .logo-film > svg, .logo-film > span, .logo-film > span {
  transition: all .3s ease-in-out;
}

.logo-film:hover > svg, .logo-film:focus > svg, .logo-film:hover > span, .logo-film:focus > span {
  color: #ff001b;
  stroke: #ff001b;
  text-shadow: 0 0 15px #ff001b;
}

.logo_header {
  stroke: #fff;
  margin-right: 10px;
}

.logo_name {
  display: none;
}

.logo_container > a {
  align-items: center;
  display: flex;
}

.navigation_list {
  display: flex;
}

.navigation_list_item {
  text-transform: uppercase;
  font-family: Roboto;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.17px;
}

.navigation_list_item:not(:last-child) {
  margin-right: 32px;
}

.header_search_form {
  justify-content: center;
  padding-bottom: 20px;
  display: flex;
}

.search_form {
  position: relative;
}

.form-input {
  color: #fff;
  width: 280px;
  background: none;
  border: none;
  border-bottom: .5px solid #fff;
  outline: 0;
  padding: 10px;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  position: relative;
}

.form-input:focus-within {
  border-bottom: 1px solid #ff001b;
  border-radius: 5px;
  box-shadow: 0 0 15px #ff001b;
}

.form-input::placeholder {
  color: #fff;
}

.search-btn {
  stroke: #fff;
  fill: #fff;
  width: 22px;
  height: 22px;
  background: url("search.082eef79.svg") center no-repeat;
  border: none;
  border-radius: 50%;
  position: absolute;
  bottom: 10px;
  right: 10px;
  transform: scale(1);
}

.search-btn:hover, .search-btn:focus {
  transform: scale(1.2);
}

.site_nav_link_current {
  display: block;
  position: relative;
}

.site_nav_link_current:after {
  content: "";
  width: 100%;
  height: 3px;
  background-color: #ff001b;
  border: none;
  transition: width .2s ease-in-out;
  display: block;
  position: absolute;
  bottom: -10px;
  left: 0;
  transform: translateY(1px);
}

.site_nav_link_current:hover:after, .site_nav_link_current:focus:after {
  width: 0;
}

.input-error {
  color: #ff001b;
  justify-content: center;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.14;
  display: flex;
}

